* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

body {
  width: 1200px;
  text-align: center;
  justify-content: center;
  margin: 0 auto;
  display: flex;
}

.container {
  width: 60%;
  height: 100%;
  justify-content: center;
  margin: 0 auto;
  padding-top: 200px;
  display: flex;
}

.timer {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.timer__block {
  width: 200px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
}

.timer__block .time {
  color: #191818;
  height: 170px;
  background-color: #d5e711;
  justify-content: center;
  align-items: center;
  font-size: 95px;
  display: flex;
}

.timer__block .title {
  color: #191818;
  text-align: center;
  background-color: #d6a30b;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  padding: 5px;
}

.btn {
  justify-content: first baseline;
  min-width: 900px;
  margin: 0, auto;
  align-items: center;
  margin-top: 150px;
  display: flex;
}

.btn .button {
  background-repeat: 10px;
  width: 180px;
  cursor: pointer;
  border: none;
  padding: 10px;
  font-size: 18px;
  transition: all 300s;
}

.btn .button.start, .btn .button.stop {
  color: #d5e711;
}

.btn .button.start:hover, .btn .button.stop:hover {
  color: #fff;
  background-color: #d6a30b;
}

.btn .button.pause:hover {
  color: #fff;
  background-color: #1c37d0;
}

.btn .button.new.disabled {
  background-color: none;
  cursor: not-allowed;
}

.results__block {
  width: 300px;
  color: mainColor2;
  text-align: center;
  border: 2px solid #d5e711;
  padding: 15px;
  font-size: 18px;
  position: absolute;
  bottom: 0;
  left: 15px;
}

.results__block .results__info {
  margin: 10px 0;
  font-size: 16px;
}

